import React from "react";
import AdminMenu from "../../components/AdminMenu/AdminMenu";
import Nav from "../../components/Navbar/Nav";
import { useState, useEffect } from "react";
import toast from "react-hot-toast";
import axios from "axios";
import CategoryForm from "../../components/Forms/CategoryForm";
import { Modal } from "antd";

const CreateCategory = () => {
  const [categories, Setcategories] = useState([]);
  const [name, Setname] = useState("");
  const [Visible, SetVisible] = useState(false);
  const [selected, Setselected] = useState(null);
  const [updatedName, SetupdatedName] = useState("");

  const getCategories = async () => {
    try {
      const { data } = await axios.get("/api/admin/category/get-category");
      if (data?.success) {
        Setcategories(data?.categories);
        toast.success("categories accquired successfully");
      }
    } catch (error) {
      console.log(error);
      toast.error("cannot get categories");
    }
  };
  const HandleSubmit = async (e) => {
    e.preventDefault();
    try {
      const { data } = await axios.post("/api/admin/category/create-category", {
        name,
      });
      if (data?.success) {
        toast.success(`${name} is created`);
        getCategories();
        Setname(" ");
      } else {
        toast.error(data.message);
      }
    } catch (error) {
      console.log(error);
      toast.error("something went wrong");
    }
  };

  const HandleUpdate = async (e) => {
    e.preventDefault();
    try {
      const { data } = await axios.put(
        `/api/admin/category/update-category/${selected._id}`,
        { name: updatedName }
      );
      if (data?.success) {
        toast.success(`nice! choise,  ${updatedName}  is updated `);
        Setselected(null);
        SetupdatedName("");
        SetVisible(false);
        getCategories();
      } else {
        toast.error(data.message);
      }
    } catch (error) {
      console.log(error);
      toast.error("something went wrong");
    }
  };

  const HandleDelete = async (id) => {
    try {
      const yesDelete = window.prompt(
        `are you sure to delete  this  category ,TYPE "YES" `
      );
      if (yesDelete === "YES") {
        const { data } = await axios.delete(
          `/api/admin/category/delete-category/${id}`
        );
        if (data?.success) {
          toast.success(` SELECTED CATEGORY IS DELETED SUCCESSFULLY `);

          getCategories();
        } else {
          toast.error(data.message);
        }
      }
    } catch (error) {
      console.log(error);
      toast.error("something went wrong");
    }
  };
  useEffect(() => {
    getCategories();
  }, []);
  return (
    <div>
      <Nav />
      <div className="admin_main">
        <hr />
        <div className="d-flex row container-fluid p-3 ">
          <div className="col-md-3">
            <AdminMenu />
          </div>
          <div className="col-md-9 ps-5  ">
            <div className=" p-3 w-85 ps-5 ">
              <h1 className="mb-3 text-uppercase  fw-bold   font-monospace">
                manage categories
              </h1>
              <hr />
              <div className="p-3 w-50">
                <CategoryForm
                  HandleSubmit={HandleSubmit}
                  value={name}
                  Setvalue={Setname}
                />
              </div>
              <hr />
              <div className="w-75">
                <table class="table">
                  <thead>
                    <tr>
                      <th scope="col" className="ps-4 ">
                        ID
                      </th>
                      <th scope="col" className="ps-4 ">
                        Name
                      </th>
                      <th scope="col" className="ps-4 ">
                        Action{" "}
                      </th>
                    </tr>
                  </thead>
                  <tbody>
                    {categories.map((data) => (
                      <tr className="p-3 m-4">
                        <td className="p-3">{data._id}</td>
                        <td className="p-3 m-4 text-uppercase" key={data._id}>
                          {data.name}
                        </td>

                        <td className="p-3">
                          <button
                            type="button"
                            className="btn btn-primary m-1 ms-3"
                            onClick={() => {
                              SetVisible(true);
                              SetupdatedName(data.name);
                              Setselected(data);
                            }}
                          >
                            Edit
                          </button>
                          <button
                            type="button"
                            className="btn btn-danger m-1 ms-3"
                            onClick={() => {
                              HandleDelete(data._id);
                            }}
                          >
                            Delete
                          </button>
                        </td>
                      </tr>
                    ))}
                  </tbody>
                </table>
              </div>

              <Modal
                onCancel={() => SetVisible(false)}
                footer={null}
                visible={Visible}
              >
                <CategoryForm
                  value={updatedName}
                  Setvalue={SetupdatedName}
                  HandleSubmit={HandleUpdate}
                />
              </Modal>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default CreateCategory;
