import React from "react";

const CategoryForm = ({ HandleSubmit, value, Setvalue }) => {
  localStorage.setItem("previousCategory", value);
  return (
    <>
      <form onSubmit={HandleSubmit}>
        <div class="mb-3">
    
          <input
            type="text"
          
            autoComplete="off"
            className="form-control py-3 fw-bolder"
            id="exampleFormControlInput1"
            placeholder="Enter new category"
            value={value}
            
            onChange={(e) => Setvalue(e.target.value)}
          />
        </div>
        <div class="mb-3">
          <button
            type="submit"
            className="btn btn-primary ms-2 px-5  py-2"
          >
            submit
          </button>
        </div>
      </form>
    </>
  );
};

export default CategoryForm;
